import {ColorVariantType} from '../common/common.models';

export type AdaptProgressVariantType = ColorVariantType | 'not-completed' | 'default';

export interface AdaptProgressIndicator {
  /**
   * Title of the indicator
   */
  title?: string;

  /**
   * Minimum width for title of indicator in pixels. This value is used for correct positioning of indicator title in case there is not enough space
   */
  min_width?: number;

  /**
   * Current value of the indicator.
   */
  value_now: number;
}

export interface AdaptProgressSegmentConfig {
  /**
   * Adds animation.
   */
  animated?: boolean;

  /**
   * Tooltip string, which appears above segment. Empty value removes the tooltip.
   */
  title?: string;
  // TODO: Refactor this if possible

  /* eslint-disable @typescript-eslint/no-explicit-any */
  /**
   * Callback, firing on click/keypress. Sends back $event and the object of the clicked segment.
   */
  action?: any;
  /* eslint-enable @typescript-eslint/no-explicit-any */

  /**
   * Current value of the progress.
   */
  value_now: number;

  /**
   * Type of the segment. Possible values: 'primary', 'info', 'success', 'warning', 'danger', 'not-completed'.
   */
  variant: AdaptProgressVariantType;
}

export interface AdaptProgressConfig {
  /**
   * Max value of the progress bar.
   */
  value_max?: number;

  /**
   * Min value of the progress bar.
   */
  value_min?: number;

  /**
   * The title with appears on the terminal part of the progress bar.
   */
  title?: string;

  /**
   * The number of decimal digits.
   */
  floor?: number;

  /**
   * Array of the segments for progress bar, see below.
   */
  indicators?: AdaptProgressIndicator[];

  /**
   * Array of indicators that will be shown on progress bar.
   */
  data: AdaptProgressSegmentConfig[];
}
